
.scontainer {
    background-position: center;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 5%;
}

.searchbar {
    width: 100%;
    max-width: 700px;
    background: rgba(0, 0, 0, 0.38);
    display: flex;
    align-items: center;
    border-radius: 60px;
    padding: 10px 20px;
    backdrop-filter: blur(4px) saturate(180%);
}

.searchinput {
    background: transparent;
    flex: 1;
    border: 0;
    outline: none;
    padding: 24px 20px;
    font-size: 20px;
    color: #ffffff;
}

::placeholder{
    color: #cac7ff;
}

.searchbarbtn {
    border: 0;
    border-radius: 50%;
    width: 60px;
    height: 60px;
    background: rgba(0, 0, 0, 0.38);
    ;
    cursor: pointer;
    
}

button:hover {
    color: rgb(15, 119, 119);
}

.card { 
    width: 50rem;
    border-radius: 20px;
    background-color: rgba(245, 245, 245, 0.4); }
.card-header { opacity: 1;}

.cardContainer {
    width: 100%;
    max-width: 700px;
    background: rgba(0, 0, 0, 0.38);
    display: flex;
    align-items: center;
    border-radius: 60px;
    padding: 10px 20px;
    backdrop-filter: blur(4px) saturate(180%);
}

.cardcont {
    background: transparent;
    flex: 1;
    border: 0;
    outline: none;
    padding: 24px 20px;
    font-size: 20px;
    color: #ffffff;
}

.card-header, .card-footer{
    border: 0;
    max-width: 50rem;
}
.card-title{
    margin: 0;
}